html {
  box-sizing: border-box;
}

*::after,
::before {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", "Arial", sans-serif;
  padding: 0;
  color: #000000;
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  background-color: #ffffff;
  margin: 0 auto;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 960px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
  box-sizing: border-box;
}

.container--main {
  max-width: 960px;
  width: 100%;
  margin: 0 auto;
  box-sizing: border-box;
  padding: initial;
}

@media (max-width: 959px) {
  .container--mobile {
    padding: 0;
  }
}

@media (min-width: @desktop-width) {
  .page-header .container {
    max-width: 960px;
    width: 100%;
    padding: 0 20px;
    margin: 0 auto;
    display: flex;
    box-sizing: border-box;
  }
}

@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  src: local("Open Sans"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}

@font-face {
  font-family: "Open Sans";
  font-weight: 700;
  src: local("Open Sans Bold"), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}

@font-face {
  font-family: "Open Sans";
  font-weight: 300;
  src: local("Open Sans Light"), url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
}
